(function () {

  'use strict';

  /*@ngInject*/
  function InputErrorMessagesDirective () {
    return {
      templateUrl: '/app/components/inputError/inputErrorMessages.html',
      require: ['^form'],
      restrict: 'A',
      transclude:true,
      scope:true,
      /* @ngInject */
      controller:function($scope){
        $scope.message = null;
        $scope.valid = true;
        $scope.attr = $scope.attr || null;
        this.setError = function(val, type){
          $scope.error = val;
          $scope.type = type;
          $scope.message = $scope.attr[val] || null;
          $scope.valid = false;
        };

        this.removeError = function(){
          $scope.error = null;
          $scope.valid = true;
        };
      },
      link: function (scope, element, attr) {
        scope.attr = attr;
        scope.$watch('valid', function (validity) {
          if (validity) {
            element.removeClass('error');
          } else {
            element.addClass('error');
          }
        });
      }
    };
  }

  angular.module('orcamentoBoxFrontend')
    .directive('inputErrorMessages', InputErrorMessagesDirective);

})();


(function () {

  'use strict';

  function InputErrorDirective() {
    return {
      require: ['ngModel', '^form', '^?inputErrorMessages'],
      restrict: 'A',
      link: function (scope, element, attrs, controllers) {
        var model = controllers[0];
        var form = controllers[1];
        var groupError = controllers[2];
        var email = /\S+@\S+\.\S+/;
        var sameAs = attrs.sameAs || null;
        var validateCpf = attrs.validateCpf || null;
        var validateCnpj = attrs.validateCnpj || null;
        var validatePhone = attrs.validatePhone || null;
        var minValue = attrs.minValue || null;
        var minLength = attrs.minLength || null;
        var maxValue = attrs.maxValue || null;

        scope.numberType = attrs.numberType;

        if (scope.numberType === 'int') {
          model.$parsers.push(function (inputValue) {
            var transformedInput = '';
            if (angular.isUndefined(inputValue)) {
              return '';
            }
            transformedInput = inputValue.replace(/[^0-9]/g, '');
            if (transformedInput !== inputValue) {
              model.$setViewValue(transformedInput);
              model.$render();
            }
            return transformedInput;
          });
        }

        function isValidPHONE(strPhone) {
          if(strPhone === null || strPhone === ''){
            return true;
          }
          //Menos de 2 carácteres
          var input = String(strPhone);
          if(input.length < 2){
            return false;
          }
          //Se o dígito é menor que 11
          var ddd = parseInt(input.substring(0,2));
          if(ddd < 11){
            return false;
          }
          //Se o primeiro dígito (fora o DDD é menor que 2)
          var first = input.charAt(2);
          if(first === '' || first < 2){
            return false;
          }
          //Se tem menos que 8 dígitos
          var rest = input.substring(2,input.length);
          if(rest.length < 8){
            return false;
          }
          //Se não é uma sequência de números repetidos
          if(rest.match(/^([0-9])\1*$/) != null){
            return false;
          }
          return true;
        }



        // http://www.receita.fazenda.gov.br/aplicacoes/atcta/cpf/funcoes.js
        function isValidCPF(strCPF) {
          var Soma = 0;
          var Resto;
          strCPF = String(strCPF).replace(/\D/g, '');
          if (strCPF === '00000000000') {
            return false;
          }
          for (var i = 1; i <= 9; i++) {
            Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (11 - i);
          }
          Resto = (Soma * 10) % 11;
          if ((Resto === 10) || (Resto === 11)) {
            Resto = 0;
          }
          if (Resto !== parseInt(strCPF.substring(9, 10))) {
            return false;
          }
          Soma = 0;
          for (i = 1; i <= 10; i++) {
            Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (12 - i);
          }
          Resto = (Soma * 10) % 11;
          if ((Resto === 10) || (Resto === 11)) {
            Resto = 0;
          }
          if (Resto !== parseInt(strCPF.substring(10, 11))) {
            return false;
          }
          return true;
        }

        function isValidCNPJ(cnpj) {

          cnpj = String(cnpj).replace(/[^\d]+/g, '');

          if(cnpj === null || cnpj === ''){
            return true;
          }

          if (cnpj.length != 14){
            return false;
          }


          // Elimina CNPJs invalidos conhecidos
          if (cnpj === '00000000000000' ||
            cnpj === '11111111111111' ||
            cnpj === '22222222222222' ||
            cnpj === '33333333333333' ||
            cnpj === '44444444444444' ||
            cnpj === '55555555555555' ||
            cnpj === '66666666666666' ||
            cnpj === '77777777777777' ||
            cnpj === '88888888888888' ||
            cnpj === '99999999999999'){
            return false;
          }

          // Valida DVs
          var tamanho = cnpj.length - 2;
          var numeros = cnpj.substring(0, tamanho);
          var digitos = cnpj.substring(tamanho);
          var soma = 0;
          var pos = tamanho - 7;
          for (i = tamanho; i >= 1; i--) {
            soma += numeros.charAt(tamanho - i) * pos--;
            if (pos < 2){
              pos = 9;
            }
          }
          var resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
          if (resultado !== parseInt(digitos.charAt(0))){
            return false;
          }

          tamanho = tamanho + 1;
          numeros = cnpj.substring(0, tamanho);
          soma = 0;
          pos = tamanho - 7;
          for (var i = tamanho; i >= 1; i--) {
            soma += numeros.charAt(tamanho - i) * pos--;
            if (pos < 2){
              pos = 9;
            }
          }
          resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;

          return resultado === parseInt(digitos.charAt(1));
        }

        var getFirstError = function (formError) {
          if (!formError) {
            return false;
          }
          return Object.keys(formError)[0];
        };

        scope.$watch(function () {
          return model.$viewValue || model.$touched || form.$submitted;
        }, function () {
          var ok = null;
          if (element[0].type === 'email') {
            ok = email.test(element.val());
            model.$setValidity('email', ok);
          }

          if (sameAs) {
            model.$parsers.unshift(function (inputValue) {
              if (inputValue === form[attrs.sameAs].$modelValue) {
                model.$setValidity('sameAs', true);
                return inputValue;
              } else {
                model.$setValidity('sameAs', false);
                return undefined;
              }
            });
          }

          if (validateCpf) {
            model.$parsers.push(function (inputValue) {
              if (isValidCPF(inputValue)) {
                model.$setValidity('cpf', true);
                return inputValue;
              } else {
                model.$setValidity('cpf', false);
                return undefined;
              }
            });
          }

          if (validateCnpj) {
            model.$parsers.push(function (inputValue) {
              if (isValidCNPJ(inputValue)) {
                model.$setValidity('cnpj', true);
                return inputValue;
              } else {
                model.$setValidity('cnpj', false);
                return undefined;
              }
            });
          }

          if (validatePhone) {
            model.$parsers.push(function (inputValue) {
              if (isValidPHONE(inputValue)) {
                model.$setValidity('phone', true);
                return inputValue;
              } else {
                model.$setValidity('phone', false);
                return undefined;
              }
            });
          }

          if (minValue) {
            model.$parsers.push(function (inputValue) {
              var value = parseInt(String(inputValue).replace(/\D/g, ''));
              if (value !== '' && value < parseInt(minValue)) {
                model.$setValidity('minValue', false);
                return undefined;
              } else {
                model.$setValidity('minValue', true);
                return inputValue;
              }
            });
          }

          if (maxValue) {
            model.$parsers.push(function (inputValue) {
              var value = parseInt(String(inputValue).replace(/\D/g, ''));
              if (value > parseInt(maxValue)) {
                model.$setValidity('maxValue', false);
                return undefined;
              } else {
                model.$setValidity('maxValue', true);
                return inputValue;
              }
            });
          }

          if (minLength) {
            model.$parsers.push(function (inputValue) {
              if (inputValue.length < parseInt(minLength)) {
                model.$setValidity('minLength', false);
                return undefined;
              } else {
                model.$setValidity('minLength', true);
                return inputValue;
              }
            });
          }

          if (model.$invalid && model.$dirty || model.$invalid && (model.$touched || form.$submitted)) {
            groupError.setError(getFirstError(model.$error), element[0].type);
          } else {
            if(groupError){
              groupError.removeError();
            }
          }
        });
      }
    };
  }

  angular.module('orcamentoBoxFrontend')
    .directive('inputError', InputErrorDirective);

})();

