(function() {
  'use strict';

  angular
    .module('orcamentoBoxFrontend')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    var header = {
      templateUrl: 'app/components/navbar/navbar.html',
      controller: 'NavbarController',
      controllerAs: 'header'
    };

    var footer = {
      templateUrl: 'app/components/footer/footer.html',
      controller: 'FooterController',
      controllerAs: 'footer'
    };


    $stateProvider

      .state('root', {
        abstract: true,
        deepStateRedirect: true,
        'views': {
          'header': header,
          'container@': {
            template: '<ui-view/>'
          },
          'footer': footer
        }
      })

      .state('root.home', {
        url: '/',
        views: {
          'container@': {
            templateUrl: 'app/main/main.html',
            controller: 'MainController',
            controllerAs: 'main'
          }
        }
      })
      .state('root.howitworks', {
        url: '/como-funciona',
        views: {
          'container@': {
            templateUrl: 'app/how-it-works/how-it-works.html',
            controller: 'HowController',
            controllerAs: 'how'
          }
        }
      })
      .state('root.joinus', {
        url: '/junte-se',
        views: {
          'container@': {
            templateUrl: 'app/join-us/join-us.html',
            controller: 'JoinController',
            controllerAs: 'join'
          }
        }
      })
      .state('root.contact', {
        url: '/contato',
        views: {
          'container@': {
            templateUrl: 'app/contact/contact.html',
            controller: 'ContactController',
            controllerAs: 'contact'
          }
        }
      })

      .state('root.generic', {
        url: '/:page',
        resolve: {

        },
        views: {
          'container@' : {
            templateUrl: 'app/city/home.html'
          }
        }
      })

      .state('root.account', {
        abstract: true,
        deepStateRedirect: true,
        url: '/conta',
        'views': {
          'header': header,
          'container@': {
            template: '<ui-view/>'
          },
          'footer': footer
        }
      })
      .state('root.account.login', {
        url: '/entrar',
        views: {
          'container@': {
            templateUrl: 'app/account/login/login.html',
            controller: 'LoginController',
            controllerAs: 'login'
          }
        }
      })
      .state('root.account.signup', {
        url: '/cadastrar',
        views: {
          'container@': {
            templateUrl: 'app/account/signup/signup.html',
            controller: 'SignupController',
            controllerAs: 'signup'
          }
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

